.containerSlide {
  width: 35em;
  margin: 0 0 1em;
  border: solid black;
  border-radius: 1em;
  margin-bottom: 1em;
}

.each-slide {
  width: 50%;
  margin: auto;
}
.containerSlide img {
  width: 80%;
}

.slideshow, .slideshowMedium, .slideshowSmall {
  margin: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}